html
{
  scroll-snap-type: y mandatory;
}

.App
{
  background-color: black;
  height: 100vh;
  display: grid;
  place-items: center;
}

.app_videos
{
  height: 100vh;
  max-height: 800px;
  width: 80%;
  max-width: 500px;
  border: 1px solid black;
  border-radius: 20px;
  overflow: scroll;
  scrollbar-width: none;
  scroll-snap-type: y mandatory;
  position: relative;
}

/* Esconde a scroll bar no chrome, safari e opera */
.app_videos::-webkit-scrollbar
{
  display: none;
}

/*Esconde a scrollbar no IE, edge e firefox*/

.app_videos
{
  -ms-overflow-style: none;
  scrollbar-width: none;
}

@media only screen and (max-width: 440px)
{
  .app_videos{
    width: 100%;
    height: 100vh;
  }
}